import React, { Component } from "react";
import { Animated } from "react-animated-css";
import ImageInformatica from "./ImageInformatica";

class Hadware extends Component {
  state = {};
  render() {
    return (
      <div className="modulos_informatica " id="modulo-hardware">
        <div className="modulo_content">
          <div className="bloco_1">
            <Animated
              animationIn="fadeIn"
              animationOut="fadeOut"
              isVisible={true}
              animationInDuration={1500}
            >
              <h1>Hardware</h1>
            </Animated>
            <Animated
              animationIn="slideInUp"
              animationOut="fadeOut"
              isVisible={true}
              animationInDuration={1000}
            >
              <div className="resumo">
                <p>
                  Aprenda e amplie seus conhecimentos sobre o hardware de
                  computador entendendo as funes especficas de cada pea e
                  quais so recomendadas na hora de fazer um upgrade em seu
                  computador.
                </p>
              </div>
            </Animated>
            <Animated
              animationIn="fadeInUp"
              animationOut="fadeOut"
              isVisible={true}
              animationInDuration={1200}
            >
              <div className="conteudo">
                <h3>Contedo Programtico</h3>
                <div>
                  <li>Instalao de Dispositivos e Perifrico</li>
                  <li>Instalao de Dispositivos</li>
                  <li>Normas de Segurana </li>
                  <li> Montagem da CPU</li>
                  <li> Troca de Peas </li>
                  <li>Estrutura da Placa Me</li>
                  <li>Tipos de HD</li>
                  <li> Conceito de Memria Ram</li>
                  <li> Conceito de Processadores </li>
                  <li>Fontes e Alimentao da CPU</li>
                  <li>Dicas de Compra de Equipamentos</li>
                </div>
              </div>
            </Animated>
          </div>

          <div className="bloco_2">
            <figure>
              <Animated
                animationIn="slideInRight"
                animationOut="fadeOut"
                isVisible={true}
                animationInDuration={1000}
              >
                <ImageInformatica
                  imgDesktop={"modulo_hardware.png"}
                  imgMobile={"modulo_hardware-mob.png"}
                />
              </Animated>
            </figure>
          </div>
        </div>
      </div>
    );
  }
}

export default Hadware;
