import React, { Component } from "react";
import { Animated } from "react-animated-css";

class SobreIngles extends Component {
  state = {};
  render() {
    return (
      <div className="sub_pages_ingles" id="sub_page_sobre_ingles">
        <div>
          <Animated
            animationIn="fadeInUp"
            animationOut="fadeOut"
            isVisible={true}
            animationInDuration={800}
          >
            <h3>Sobre Nosso Curso</h3>
            <p>
            Nosso curso de ingls  focado na conversao, que  o que a
              maioria das pessoas buscam ao aprender ingls, todo curso foi
              planejado para que o aluno consiga buscar seu prprio conhecimento
              de ingls, de forma personalizada de acordo com suas necessidades
.{" "}
            </p>
            <p>
            o nosso curso foi feito para voc aprender ingls da mesma forma
              que poliglotas (pessoas que falam vrios idiomas), aprendem, o
              curso dura trs anos e  dividido e 3 etapas: como funcionam as
              aulas: as trabalham com as 4 habilidades, ler e escrever, ouvir e
              falar.

            </p>
          </Animated>
          <Animated
            animationIn="fadeInUp"
            animationOut="fadeOut"
            isVisible={true}
            animationInDuration={1000}
          >
            <p>
              <b>ler e escrever:</b> alem do que o professor ensinar, sera usado
              textos de revistas e jornais, livros e sites, o aluno sera
              estimulado a escrever em ingles, desde de uma simples anotao ate
              redaoes, assim o aluno ja aprende a usar o idioma no dia a dia,
              desde a primeira aula.
              <br />
              <b>ouvir e falar:</b> Este  o foco do nosso curso e,  a
              principal parte do aprendizado de um idioma, e por isso nossos
              alunos sao incentivados a aprender ouvir e depois falar sobre o
              que ouviu, com muitas aulas de conversao, onde o professor fara
              uso de varios recursos desde videos, filmes, musicas, entrevistas
              em ingles. Tambem vamos prepar nossos alunos para saber se
              sobressair, sobre situaoes onde podera usar uma coversa em
              ingles, como fazer atendimento a um clinte, pedir informaoes e
              dar informaoes, ou fazer uma entrevista de emprego em ingles.
            </p>
          </Animated>
        </div>
        <Animated
          animationIn="fadeIn"
          animationOut="fadeOut"
          isVisible={true}
          animationInDuration={2000}
        >
          <div id="ler" className="fundamentos_ingles">
            <div className="content_destaque">
              <h3>
                <span>
                  <img
                    src={require("../../../../assets/img/english/ico-read.png")}
                  />
                </span>
                Ler
              </h3>
              <p>
                A leitura em Ingls, por exemplo, melhora muito o vocabulrio,
                facilita a formao de frases, alm de estimular o aprendizado.
              </p>
            </div>
          </div>
          <div id="ouvir" className="fundamentos_ingles">
            <div className="content_destaque">
              <h3>
                <span>
                  <img
                    src={require("../../../../assets/img/english/ico-fone.png")}
                  />
                </span>
                Ouvir{" "}
              </h3>
              <p>
                Listening (Ouvir)  de suma importncia e nosso curso traz ao
                aluno exemplos prticos e imersivos auditivos desde a primeira
                aula.
              </p>
            </div>
          </div>
          <div id="falar" className="fundamentos_ingles">
            <div className="content_destaque">
              <h3>
                <span>
                  <img
                    src={require("../../../../assets/img/english/ico-talk.png")}
                  />
                </span>
                Falar
              </h3>
              <p>
                O conhecimento da pronncia correta das palavras, alm do ritmo
                e sonoridade das frases  essencial para aqueles que desejam
                conversar em ingls com menos dificuldade. o que faz da
                pronncia um dos mdulos fundamentais do nosso curso.
              </p>
            </div>
          </div>
          <div id="escrever" className="fundamentos_ingles">
            <div className="content_destaque">
              <h3>
                <span>
                  <img
                    src={require("../../../../assets/img/english/ico-book.png")}
                  />
                </span>
                Escrever
              </h3>
              <p>
                A habilidade de escrever bem em Ingls  de suma importncia,
                poes no basta ter um bom vocabulario, mas sim aprender a se
                expressar de maneira correta atravs da escrita.
              </p>
            </div>
          </div>
        </Animated>
      </div>
    );
  }
}

export default SobreIngles;
