import React, { Component } from "react";
import { Animated } from "react-animated-css";
import ImageInformatica from "./ImageInformatica";

class Office extends Component {
  state = {};
  render() {
    return (
      <div className="modulos_informatica " id="modulo-office">
        <div className="modulo_content">
          <div className="bloco_1">
            <Animated
              animationIn="fadeIn"
              animationOut="fadeOut"
              isVisible={true}
              animationInDuration={1500}
            >
              <h1>Office</h1>
            </Animated>
            <Animated
              animationIn="slideInUp"
              animationOut="fadeOut"
              isVisible={true}
              animationInDuration={1000}
            >
              <div className="resumo">
                <p>
                  Nosso curso  focado em Excel, Word e PowerPoint; O aluno
                  aprende de forma simples e direta com exemplos prticos e
                  realmente aplicveis onde enssinamos as principais ferramentas
                  que possuem tilidade tanto para uso pessoal como
                  profissional.
                </p>
              </div>
            </Animated>
            <Animated
              animationIn="fadeInUp"
              animationOut="fadeOut"
              isVisible={true}
              animationInDuration={1200}
            >
              <div className="conteudo">
                <h3>Contedo Programtico</h3>
                <div>
                  <span className="span_office">
                    <h4>Word</h4>
                    <li>Instroduo</li>
                    <li>Interface </li>
                    <li>Cabealho e Rodap</li>
                    <li>Formatao de Texto</li>
                    <li>Orientao de Pgina</li>
                    <li>Layout de Pgina</li>
                    <li>Insero de Imagens</li>
                    <li>Area de Tranferncia</li>
                    <li>Armazenamento de Arquivos</li>
                    <li>Templates de Documento</li>
                    <li>Formataao de Curriculos</li>
                    <li>Listas</li>
                    <li>TabulaoMala Direta</li>
                    <li>Sumrio automatico</li>
                    <li>Norma ABNT</li>
                    <li>Design de Pgina</li>
                    <li>Formatao com Estilo</li>
                    <li>Teclad de Atalho</li>
                    <li>Inserindo Objetos</li>
                    <li>Formas 3D</li>
                    <li>Trabalhando com Documentos</li>
                    <li>Corretor Ortogrfico</li>
                    <li>Modo de Exibio</li>
                    <li>Recurso Diga-me</li>
                  </span>
                  <span className="span_office">
                    <h4>Excel</h4>
                    <li>Interface</li>
                    <li>Area de Tranferncia</li>
                    <li>Modo de Exibio</li>
                    <li>Layout de Pgina</li>
                    <li>Pastas de Trabalho</li>
                    <li>Conceito de Planilhas</li>
                    <li>Conceitos de Tabelas</li>
                    <li>Navegao</li>
                    <li>Insero de Informaes</li>
                    <li>Operaes Aritmticas</li>
                    <li>Personalizao de Tabelas</li>

                    <li>Formatar como tabela</li>
                    <li>Formatao condicionais</li>
                    <li>Manipulao de Grficos</li>
                    <li>Mini Grficos</li>
                    <li>Trabalhando com Planilhas</li>
                    <li>Trabalhando com Tabelas</li>
                    <li>Tabela Dinmica</li>
                    <li>Funes Condicionais</li>
                    <li>Formulas e Funes </li>
                    <li>Hyper Links</li>
                    <li>Funes Financeiras</li>
                    <li>Clculo com Data e Hora</li>
                    <li>Atalhos</li>
                    <li>Seleo Inteligente</li>
                    <li>Filtro e Classificaao</li>
                    <li>Impresso de Relatrios</li>
                  </span>
                  <span className="span_office">
                    <h4>Powerpoint</h4>
                    <li>Instroduo Power Point</li>
                    <li>Interface</li>
                    <li>Modo de Apresentao</li>
                    <li>Modo de Edio</li>
                    <li>Criao de Slides</li>
                    <li>Efeito de Animao</li>
                    <li>Efeito de Transio</li>
                    <li>Design de Slide </li>
                    <li>Insero de Objetos</li>
                    <li>Insero de Vdeos</li>
                    <li>Objetos 3D</li>
                    <li>Gravao de Slides</li>
                    <li>Apresentao em Vdeo</li>
                    <li>Hyperlinks</li>
                    <li>Telas Compartilhadas</li>
                    <li>Teclas de Atalhos</li>
                    <li>Dicas de Apresentaao</li>
                    <li>Dicas de Seminrios</li>
                  </span>
                </div>
              </div>
            </Animated>
          </div>
          {/*bloco 1 */}

          <div className="bloco_2">
            <figure>
              <Animated
                animationIn="slideInRight"
                animationOut="fadeOut"
                isVisible={true}
                animationInDuration={1000}
              >
                <ImageInformatica
                  imgDesktop={"modulo_office.png"}
                  imgMobile={"modulo_office-mob.png"}
                />
              </Animated>
            </figure>
          </div>
        </div>
      </div>
    );
  }
}

export default Office;
